* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
  background-color: #f7f6f6;
}

.main {
  background-image: url('./images/background.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: 50% 40%;
}


h1 {
  font-size: 40px;
}

h3 {
  font-size: 25px;
}

a {
  color: #3f8c83;
}

ul {
  padding-left: 0;
}

li {
  list-style-type: none;
  padding: 10px;
  text-decoration: none;
}

.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 40px;
}

.btn {
  color: white;
  background-color: #e86c6d;
  border: none;
  font-size: 20px;
  margin-left: auto;
  margin-right: auto;
  margin: 20px;
  padding: 10px;
}

.btn-transparent {
  color: #e86c6d !important;
  background: none;
  border: solid;
  border-radius: 5px;
  padding: 5px;
}

.btn-transparent:hover {
  color: white !important;
  background: #e86c6d;
  border: solid;
  border-color: #e86c6d;
  border-radius: 5px;
  padding: 5px;
}

button:disabled {
  opacity: 0.6;
}

/* Login / Register */

.login-register-container {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.login-register-logo {
  height: 100px;
}

.login-register-header-container {
  color: #e86c6d;
}

.login-register-header {
  margin: 10px;
}

.login-register-form-container {
  background-color: white;
  margin-top: 30px;
  border-radius: 30px;
  -webkit-box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.2);
  max-width: 40%;
  margin-left: auto;
  margin-right: auto;
  z-index: 1;
  overflow: hidden;
}

.login-register-form-header {
  margin: 0;
  background-color: #e86c6d;
  padding: 20px;
}

form {
  padding: 30px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.login-register-form-content p{
  margin: 0;
}

.login-register-form-input {
  width: 100%;
  padding: 15px;
  margin: 10px;
  border-radius: 10px;
  border: none;
  background-color: #f1f1f1;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.login-register-button {
  width: 60%;
  border-radius: 15px;
}

.footer-text {
  margin: 0;
}

/* Add Listing */

.add-listing-container {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: 1.2fr 0.8fr;

}

.add-listing-form-container {
  /* background-color: white; */
  -webkit-box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.2);
  background-color: #f7f6f6;
  width: 90%;
  margin-right: auto;
}

.add-listing-form-header h1 {
  margin: 0;
}

.add-listing-form-header p {
  margin: 10px 0;
}

.add-listing-form-content {
  padding: 20px 40px;
}

.add-listing-form-content h1{
  color: #e86c6d;
}

.add-listing-form-content p{
  color: #8a8888;
}

.add-listing-form-content hr{
  width: 25%;
  margin-left: 0;
  height: 5px;
  border: 0;
  border-top: 4px solid #e86c6d;
  padding: 0;
}

.add-listing-form-fields {
  display: grid;
  margin: 0;
  padding: 10px;
  grid-template-rows: 1fr;
  grid-gap: 20px;
}

.checkbox-container {
  margin-top: 10px;
}

.step3{
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.add-listing-form-fields h3 {
  margin: 5px 0;
}

.add-listing-form-fields p{
  color: black;
  margin: 0;
}


.add-listing-form-input-container {
  display: grid;
  grid-template-rows: 1fr;
  grid-gap: 15px;
}

.add-listing-form-input-container input, select {
  padding: 15px;
  width: 100%;
  margin: 5px;
  border-radius: 5px;
  border: none;
  font-size: 14px;
  background-color: white;
}

.add-listing-form-input-container select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  font-size: 14px;
}

.add-listing-form-button {
  margin: 10px;
  border-radius: 5px;
  width: 30%;
}

.add-listing-message-container {
  display: grid;
  grid-template-rows: 0.2fr 1.8fr;
  /* top:0;
  bottom: 0;
  left: 0;
  right: 0; */
  margin-left: auto;
  width: 90%;
  text-align: right;
}

.add-listing-message-content {
  margin-top: 4em;
}

.add-listing-message-content h1 {
  color: #3f8c83;
  margin-bottom: 5px;
}

.add-listing-message-content p {
  margin-left: auto;
  width: 60%;
  font-size: 18px;
}

.add-listing-form-input-header {
  margin: 20px 0;
}

.label-container {
  margin-bottom: 15px;
}

/* .checkbox {
  width: 100%;
} */

.checkbox input[type="checkbox"] {
  width: 10px !important;
}

.checkbox input[type="text"] {
  background: transparent;
  border: none;
  border-bottom: 1px solid #000000;
  width: 100px;
  padding: 0;
  border-radius: 0;
  margin-left: 10px;
}

/* Thank you page */
.add-listing-form-content.thank-you {
  display: grid;
  grid-template-rows: 1fr;
  grid-gap: 30px;
}

.add-listing-form-body.thank-you h4 {
  font-size: 20px;
  width: 80%;
}

.add-listing-container.thank-you p {
  color: black;
}

/* Navigation */

.nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-decoration: none;
  padding-top: 10px;
  padding-bottom: 20px;
  text-transform: uppercase;
}

.nav-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.nav-container li {
  display: inline;
}

.nav-container.two {
  margin-left: auto;
}

.nav-container li a {
  color: #8a8888;
  text-decoration: none;
}

.nav-container li .active {
  color: #e86c6d;
}

.nav-logo {
  height: 50px;
}

.nav-menu-icon {
  height: 40px;
}



